.l-header {
  z-index: 100;
  padding-inline: 40px;
  padding-top: 40px;
  position: fixed;
  width: 100%;
  /* スクリーンリーダー */
}
.l-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-header__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .l-header__nav {
    display: none;
  }
}
.l-header__menu {
  display: flex;
  align-items: center;
  flex-direction: row;
  font-family: Arial, sans-serif;
  text-transform: uppercase;
}
.l-header__menu-item {
  margin-right: 40px;
}
.l-header__menu-link {
  position: relative;
}
.l-header__menu-link:hover::before {
  transform: scaleX(1);
  transform-origin: left center;
}
.l-header__menu-link::before {
  position: absolute;
  bottom: -1.2rem;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #111111;
  content: "";
  transition: transform 0.5s cubic-bezier(0.33, 1, 0.68, 1);
  transform: scaleX(0);
  transform-origin: right center;
}
.l-header__submenu {
  padding-top: 100px;
  width: 639px;
}
.l-header__submenu-inner {
  display: flex;
  flex-direction: row;
}
.l-header__submenu-image {
  width: 289px;
  height: auto;
}
.l-header__submenu-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.l-header__submenu-list {
  width: 350px;
}
.l-header__submenu-item {
  height: 33.3%;
  padding: 2em 2em 1em 2em;
}
.l-header__submenu-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.l-header__submenu-link:hover {
  color: #88b999;
}
.l-header__submenu-title {
  display: inline-block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.2rem;
}
.l-header__submenu-title .eng {
  font-family: Arial, sans-serif;
  font-size: 2.2rem;
  font-weight: bold;
  text-transform: none;
}
.l-header__hamburger-btn {
  all: unset;
  outline: revert;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  width: 46px;
  aspect-ratio: 1/1;
  display: grid;
  place-items: center;
  color: black;
  justify-items: start;
}
@media (min-width: 1024px) {
  .l-header__hamburger-btn {
    display: none;
  }
}
.l-header__hamburger-btn-bar, .l-header__hamburger-btn::before, .l-header__hamburger-btn::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background-color: currentColor;
  grid-area: 1/1;
  transition: transform 0.5s;
  text-align: left;
}
.l-header__hamburger-btn::before {
  transform: translateY(-12px);
  width: 100%;
}
.l-header__hamburger-btn::after {
  transform: translateY(12px);
  width: 25%;
}
.l-header__sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.is-none {
  display: none;
}

.js-submenu-trigger {
  position: relative;
}

.js-submenu {
  position: absolute;
  top: 0px;
  left: -200px;
}

.open {
  position: fixed;
  overflow: hidden;
  width: 100%;
}
.open .l-header__logo {
  z-index: 200;
}
.open .l-header__hamburger-btn-bar {
  transform: scale(0);
}
.open .l-header__hamburger-btn::before {
  transform: translateY(0) rotate(45deg);
}
.open .l-header__hamburger-btn::after {
  transform: translateY(0) rotate(-45deg);
  width: 100%;
}
.open .l-header__hamburger-btn {
  z-index: 200;
}
.open .l-mobile-nav {
  pointer-events: auto;
  opacity: 1;
}

.l-mobile-nav {
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s linear;
}

.l-header.js-view .l-header__menu {
  opacity: 1;
}
.l-header.js-hide .l-header__menu {
  opacity: 0;
  transition: opacity 0.3s linear;
}

.l-mobile-nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: 22px;
  color: #111111;
  background-color: #d9e3dd;
  padding: 20% 10% 10% 10%;
}
.l-mobile-nav__menu {
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
}
.l-mobile-nav__menu-item {
  margin-top: 1rem;
  border-bottom: 1px solid #cbcbcb;
  padding-bottom: 1rem;
}
.l-mobile-nav__submenu {
  margin-top: 1rem;
  font-size: 1.4rem;
}
.l-mobile-nav__submenu-item {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  font-weight: normal;
}
.l-mobile-nav__submenu-item::before {
  border-top: 1px solid;
  content: "";
  width: 1rem; /* 線の長さ */
  margin-right: 1rem;
}
.l-mobile-nav__subtitle {
  font-family: Arial, sans-serif;
  font-size: 1.2rem;
  font-weight: normal;
  margin-bottom: 1rem;
}
.l-mobile-nav__address {
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 2rem;
}
.l-mobile-nav__policy {
  font-size: 1.2rem;
  font-weight: normal;
  color: #b4b4b4;
}
.l-mobile-nav__btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.l-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
}

.c-arrow {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
}
.c-arrow::before, .c-arrow::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: "";
  vertical-align: middle;
}
.c-arrow--round::before {
  box-sizing: border-box;
  width: 3rem;
  height: 3rem;
  border: 1px solid;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.c-arrow--round::after {
  left: 0.8rem;
  width: 1rem;
  height: 1rem;
  border-top: 1px solid;
  border-right: 1px solid;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.c-arrow-bottom {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  margin: 0 1rem 0.2rem 1rem;
  border-bottom: 1px solid;
  border-right: 1px solid;
  transform: rotate(45deg);
}

.c-inner {
  max-width: 1220px;
  margin: 0 auto;
  padding: 10px 30px;
}

.c-btn {
  width: 140px;
  display: block;
  padding: 1em;
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  text-align: center;
  background-color: #111111;
  color: #ffffff;
  border: solid 2px #111111;
  transition: background-color 0.5s;
}
.c-btn:hover {
  background-color: #ffffff;
  color: #111111;
}

.c-btn--accent {
  width: 140px;
  display: block;
  padding: 1em;
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.3rem;
  background-color: #ebc110;
  color: #ffffff;
  border: solid 2px #ebc110;
  transition: background-color 0.5s;
}
.c-btn--accent:hover {
  background-color: #ffffff;
  color: #ebc110;
}

