/* スライド　マスク */
@keyframes slide {
  0% {
    -webkit-mask-size: 0% 100%;
    mask-size: 0% 100%;
  }
  100% {
    -webkit-mask-size: 100% 100%;
    mask-size: 100% 100%;
  }
}
@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}
.swiper-wrapper {
  position: relative;
}

.swiper-slide {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
  margin: 0 auto;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@supports ((-webkit-mask-size : 100% 100%)) {
  .swiper-slide img {
    -webkit-mask-size: 100% 100%;
    -webkit-mask-image: url(../images/home/slider_mask.jpg);
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: left center;
  }
}
@supports ((mask-size : 100% 100%)) {
  .swiper-slide img {
    mask-size: 100% 100%;
    mask-image: url(../images/home/slider_mask.jpg);
    mask-repeat: no-repeat;
    mask-position: left center;
  }
}

.swiper-slide-prev img {
  opacity: 0;
}

.swiper-slide-prev img,
.swiper-slide-duplicate-next img,
.swiper-slide-active img,
.swiper-slide-duplicate-active img {
  opacity: 1;
}
@supports ((-webkit-mask-size : 100% 100%)) {
  .swiper-slide-prev img,
.swiper-slide-duplicate-next img,
.swiper-slide-active img,
.swiper-slide-duplicate-active img {
    -webkit-mask-size: 100% 100%;
    animation: slide 2s cubic-bezier(0.4, 0, 0.2, 1) 0s forwards;
  }
}
@supports ((mask-size : 100% 100%)) {
  .swiper-slide-prev img,
.swiper-slide-duplicate-next img,
.swiper-slide-active img,
.swiper-slide-duplicate-active img {
    mask-size: 100% 100%;
    animation: slide 2s cubic-bezier(0.4, 0, 0.2, 1) 0s forwards;
  }
}

/* テキストスライド */
.p-home-kv__text-slider-slide.swiper-slide-active .p-home-kv__engText {
  animation: slideIn 0.8s cubic-bezier(0.4, 0, 0.2, 1) 0s forwards;
}

.p-home-kv__engText {
  opacity: 0;
  transform: translate(0, 0);
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translate(0.5rem, -0.1rem);
  }
  100% {
    opacity: 0.7;
    transform: translate(0, -0.1rem);
  }
}
/* スクロールアニメ */
.p-scroll {
  display: inline-block;
  position: absolute;
  right: 3rem;
  bottom: 0;
  z-index: 2;
  padding: 10px 10px 110px;
  height: 11rem;
  overflow: hidden;
  color: #fff;
}

.p-scroll::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 100px;
  background-color: #fff;
  animation: arrow 3s ease 0s infinite normal;
}

.p-scroll::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 100px;
  background-color: #111;
}

@keyframes arrow {
  0% {
    transform: translate3d(-50%, 0, 0);
  }
  60% {
    transform: translate3d(-50%, 11rem, 0);
  }
  100% {
    transform: translate3d(-50%, 11rem, 0);
  }
}
